import React, { ChangeEvent } from 'react';
import { EventWithDataHandler, Field } from 'redux-form';

import { Box, Flex } from '@rebass/grid';
import styled from 'theme';

import {
  Button,
  HighlightCodeField,
  InfoCircleIcon,
  SelectField,
  SmallText,
  Spinner,
} from 'components';
import { ISelectValue } from 'types';
import { formErrorUtil } from 'utils';

const ScriptWrapper = styled.div`
  position: relative;
  width: 100%;
  margin-top: 10px;

  .loading {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
  }

  .script-warning {
    position: absolute;
    top: 0;
    right: 0;
    padding-right: 10px;

    * {
      color: ${({ theme }) => theme.colors.normalAccent};
    }
  }
`;

const HiddenBox = styled(Box)`
  display: none;
`;

interface IProductRules {
  actionTypesOptions: Array<ISelectValue>;
  eventsOptions: Array<ISelectValue>;
  eventValue: number | string;
  handleGetRule: (field: string, value: string | ISelectValue) => void;
  actionTypeValue: string | number;
  isEventsLoading: boolean;
  isReadOnly: boolean;
  isRuleLoading: boolean;
  onChangeValues: EventWithDataHandler<ChangeEvent<any>>;
}

const ProductRules: React.FC<IProductRules> = ({
  actionTypesOptions,
  actionTypeValue,
  eventsOptions,
  eventValue,
  handleGetRule,
  isEventsLoading,
  isReadOnly,
  isRuleLoading,
  onChangeValues,
}) => {
  const scriptPlaceholder = React.useMemo(
    () => {
      let placeholder = '';

      if (!eventValue) {
        placeholder = 'Select an event to enable the editor';
      } else if (!actionTypeValue) {
        placeholder = 'Select an action type to enable the editor';
      } else {
        placeholder = 'Write your rule using JavaScript code here...';
      }

      return placeholder;
    },
    [eventValue, actionTypeValue]
  );

  return (
    <Box mx="-8px">
      <Flex
        alignItems="flex-start"
        flexWrap="wrap"
      >
        <Box width="270px" p="8px">
          <Field
            id="eventId"
            name="eventId"
            component={SelectField}
            label="Event"
            placeholder="Select Event"
            options={eventsOptions}
            isLoading={isEventsLoading}
            isClearable={false}
            onChange={onChangeValues}
            validate={[formErrorUtil.isRequired]}
          />
        </Box>
        <HiddenBox width={[1 / 3]} p="8px">
          <Field
            id="actionType"
            name="actionType"
            component={SelectField}
            label="Action Type"
            placeholder="Select Action Type"
            options={actionTypesOptions}
            isClearable={false}
            onChange={onChangeValues}
            validate={[formErrorUtil.isRequired]}
          />
        </HiddenBox>
        {eventValue && (
          <Box width={[2 / 3]} p="8px">
            <Box mb="5px" pl="9px">
              <SmallText>Action Types</SmallText>
            </Box>
            {!actionTypesOptions.length && (
              <Box mb="5px" pl="9px">
                <SmallText>No available action types for selected event</SmallText>
              </Box>
            )}
            <Flex alignItems="center" flexWrap="wrap">
              {actionTypesOptions && actionTypesOptions.map(type => (
                <Button
                  key={type.value}
                  text={type.label}
                  size="11"
                  isFocused={actionTypeValue === type.value}
                  classNames={['is-tabs']}
                  type="reset"
                  onClick={() => handleGetRule('actionType', type)}
                />
              ))}
            </Flex>
          </Box>
        )}
        <ScriptWrapper>
          {isRuleLoading && (
            <div className="loading">
              <Spinner isFixed={false} />
            </div>
          )}
          {actionTypeValue && (
            <Flex
              alignItems="center"
              className="script-warning"
            >
              <Box mr="3px">
                <InfoCircleIcon size="16" />
              </Box>
              <SmallText>
                The rule with empty script will be deleted
              </SmallText>
            </Flex>
          )}
          <Box width={[1]} p="0 10px 10px">
            <Field
              id="script"
              name="script"
              component={HighlightCodeField}
              placeholder={scriptPlaceholder}
              label="Script"
              fontSize={11}
              focused={true}
              height="calc(100vh - 300px)"
              disabled={!actionTypeValue || !eventValue || isReadOnly}
            />
          </Box>
        </ScriptWrapper>
      </Flex>
    </Box>
  );
};

export default ProductRules;
